.booksHeader{
    background-image: url(/static/media/books.ee2ed264.png);
    padding: 3%;
    background-attachment: fixed;
    background-position: 0 0;
    background-position: initial;
    min-height: 16rem;
    width: 100%;
}
.book{
    margin: 0 auto;
    width: 75%;
    border: solid 5px black;
    margin-top: 3%;
    padding: 2%;
    border-radius: 19px;
    background-color: brown;
    color: #ebd5b3;
}
.bookTitle{
    font-family: 'Gentium Book Basic', serif;
    text-align: center;
}
.bookContent{
    display: flex;
    align-items: center;
}
.bookImage{
    margin-right: 5%;
    border: solid 4px black;
}
.bookLinks{
    margin-top: 2%;
}
.bookSource{
    color: wheat;
    background-color: black;
    padding: 1%;
    border: solid wheat;
}
.saveButton{
    background-color: wheat;
    color: black;
    border: solid 2px black;
    padding: 5px;
}
body{
    background-color: #ebd5b3;
    font-family: 'Gentium Book Basic', serif;
}
.searchForm{
    text-align: center;
    background-color: #ebd5b3;
    width: 50%;
    margin-top: 1%;
    margin-left: auto;
    margin-right: auto;
    border-radius: 1rem;
    padding: 1%;
    color: brown;
    border: solid brown;
    background-color: #ebd5b3;

}
.searchButton{
    padding: 1%;
    margin-top: 1%;
    background-color: brown;
    color: white;
    width: 14%;
    border: 2px solid black;
    border-radius: 1.5rem;
}

.savedBooksContent{
    text-align: center;
    margin-top: 2%;
    padding: 1%;
    background-color: #ebd5b3;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    border-radius: 1rem;
    color: brown;
}

