.nav{
    background-color: #ebd5b3;
    position: relative;
    width: 100%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    padding: 2rem;
    display: flex;
    font-size: 1.5rem;

}
a{
    margin: 0 1% 0 1%;
    color: black;
}
.footer{
    position: relative;
    background-color: #ebd5b3;
    height: 4rem;
    bottom: 0;
    width: 100%;
    border-top: 1rem solid rgb(175, 58, 58);
    text-align: center;
    margin-top: 9rem;
}
